/* =============== VARIABLE =============== */

:root {
  /* color */
  --first-color: #16ACC9;
  --first-color-darker: #143F48;
  --second-color: #D2DA4B;
  --second-color-flashy: #42ca20;
  --text-color: #F8F7FF;
  --container-color: #9998A3;
  --rick-text: #00a4c0;
  --rick-border: #cceb83;

  /* fonts */

  --title-font: 'Zen Kurenaido', sans-serif;
  --text-font: 'Zen Kurenaido', sans-serif;
  --rick-font: 'GetSchwifty';

  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: .938rem;
  --small-font-size: .831rem;
  --smaller-font-size: .75rem;

  --spacing-font: 0.5px;
  
  /* margin top */ 
  --mt-0-25: .25rem;
  --mt-0-5: .5rem;
  --mt-0-75: .75rem;
  --mt-1: 1rem;
  --mt-1-5: 1.5rem;
  --mt-2: 2rem;
  --mt-2-5: 2.5rem;
  --mt-3: 3rem;

  /* z index */ 
  --z-tooltip: 10 ;
  --z-fixed: 100 ;
  --z-modal: 1000 ;
}

@media screen and (max-width: 1024px) {

  :root {
    --h1-font-size: 1.25rem;
    --h2-font-size: 1rem;
    --h3-font-size: .75rem;
    --normal-font-size: .60rem;
    --small-font-size: .50rem;
    --smaller-font-size: .35rem;
  
    --spacing-font: 0.25px;
  }

}

@media screen and (max-width: 480px) {

  :root {
    --h1-font-size: 1rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: .75rem;
    --small-font-size: .20rem;
    --smaller-font-size: .15rem;
  
    --spacing-font: 0.1px;
  }

}

